import config from "@/libs/config/api"

class Login {
	$u
	constructor($u) {
		this.$u = $u
	}

	/*
	
	@ params  
	type:'password'          //sms xcx app_wxAPP   password 
	mobile:'17713564637',    //
	password:'123456',       //
	code:'123456',           //
	xcx_code:'123456'        //code
	*/
	login(params = {}) {
		
		let url = '/session/' + params.type
		return new Promise((resolve, reject) => {
			this.$u.post(url, params).then(res => {
				this.$u.setVuex(config.vuexTokenField,res.token)
				resolve()
			}).catch(err => {
				//reject
				reject(err)
			})
		})
	}
	
	
	/*
	app
	@ params  
	type:'app_wx'
	openid:
	unionid:
	nice_name:
	avatar:
	*/
	appWXLogin(params = {}) {
		
		let url = '/session/app_wx'
		return new Promise((resolve, reject) => {
			this.$u.post(url, params).then(res => {
				this.$u.setVuex(config.vuexTokenField,res.token)
				console.log('wx login res--->',res)
				resolve(res)
			}).catch(err => {
				//reject
				console.log('wx login err--->',err)
				reject(err)
			})
		})
	}

	/*
	
	@ params  
	type:'xcx',
	xcx_code:
	nice_name:
	avatar:
	*/
	WXMpLogin(params = {}) {
		
		let url = '/session/xcx'
		return new Promise((resolve, reject) => {
			this.$u.post(url, params).then(res => {
				this.$u.setVuex(config.vuexTokenField,res.token)
				console.log('wx login res--->',res)
				resolve(res)
			}).catch(err => {
				//reject
				console.log('wx login err--->',err)
				reject(err)
			})
		})
	}


	logout(params = {}) {
		let url = '/session/' + this.$u.state[config.vuexTokenField].access_token
		this.$u.setVuex(config.vuexTokenField,'')
		this.$u.setVuex('vuex_user','')
		return new Promise((resolve, reject) => {
			this.$u.delete(url, params).then(res => {
				resolve()
			}).catch(err => {
				//reject
				reject(err)
			})
		})
	}

}


export default Login
